.page {
  scroll-behavior: smooth;
  box-sizing: border-box;
  min-width: 320px;
  font-family: Montserrat, sans-serif;
  overflow-x: hidden;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__body {
  background-color: #fff;
  min-width: 320px;
  margin: 0;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  z-index: 2;
  visibility: hidden;
  transition: all .5s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  visibility: visible;
  transform: translateX(0);
}

.page__overlay {
  visibility: hidden;
  pointer-events: none;
  z-index: 1;
  background-color: rgba(0, 0, 0, .5);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}

.page__menu:target ~ .page__overlay {
  visibility: visible;
}

